import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';
import { SIMILAR_SEARCH_EVENT_TAG_COLOR } from '../../../../constants/index';

const useStyles = makeStyles(theme => {
  const { right, paddingLR, left, marginLR } = theme.mixins;

  return {
    filterSection: {
      paddingBottom: theme.spacing(9),
      [theme.breakpoints.up('lg')]: {
        background: theme.mixins.White(),
        borderRadius: pxToRem(6),
        paddingBottom: '0',
      },
    },
    drawerWrapper: {
      width: '100%',
    },
    filterMobileContainer: {
      width: '100%',

      '& .MuiPaper-root': {
        width: '100%',
      },
    },
    heading: {
      color: theme.mixins.neroBlack(700),
    },
    togglefilterButton: {
      position: 'fixed',
      bottom: pxToRem(16),
      ...right(pxToRem(16)),
      zIndex: 1,

      '& button': {
        padding: `${pxToRem(8)} ${pxToRem(25)}`,
        ...theme.typography.body2Bold,
        color: theme.mixins.White(),
      },
    },
    crossIcon: {
      marginLeft: 10,
    },
    container: {
      padding: pxToRem(23),

      '& svg': {
        position: 'absolute',
        top: pxToRem(21),
        ...right(pxToRem(24)),
      },
      '& .MuiFormControlLabel-label': {
        color: theme.palette.text.tertiary,
      },
      '& .MuiFormControl-root': {
        margin: 0,
        width: '100%',
        '& .MuiFormLabel-root': {
          marginBottom: pxToRem(16),

          '&.MuiInputLabel-root': {
            top: pxToRem(-13),
            [theme.breakpoints.up('lg')]: {
              top: pxToRem(-19),
            },
          },
        },
        '& .MuiInputBase-root': {
          marginTop: 0,
        },
        '& .MuiFormGroup-root': {
          '&[role=radiogroup]': {
            flexDirection: 'column',

            '& .MuiFormControlLabel-root': {
              marginBottom: pxToRem(16),
              '& .MuiFormControlLabel-label': {
                marginLeft: 0,
              },
            },
          },
          '& .MuiFormControlLabel-root': {
            marginBottom: pxToRem(16),
            '& .MuiFormControlLabel-label': {
              marginLeft: 0,
            },
          },
          '& .MuiButtonBase-root.MuiRadio-root': {
            padding: 0,
            marginLeft: 11,
          },
          '& .MuiButtonBase-root.MuiRadio-root +  .MuiTypography-root': {
            paddingLeft: 8,
          },
        },
      },
      '&:not(:last-child)': {
        borderBottom: `1px solid ${theme.palette.border.divider}`,
      },
      '&:first-child': {
        textAlign: 'center',
        [theme.breakpoints.up('lg')]: {
          textAlign: 'initial',
          display: 'flex',
          justifyContent: 'space-between',
          color: theme.mixins.neroBlack(700),
        },
      },
    },
    eventColor: {
      '& .MuiFormGroup-root .more': {
        color: theme.mixins.ADCalendar(),
        '&::after': {
          borderColor: theme.mixins.ADCalendar(),
        },
      },
      '& .MuiFormGroup-root .less': {
        color: theme.mixins.ADCalendar(),
        '&::after': {
          borderColor: theme.mixins.ADCalendar(),
        },
      },
      '& .MuiCheckbox-root input:hover ~ span': {
        borderColor: theme.mixins.ADCalendar(),
      },
      '& .checkedIcon': {
        borderColor: theme.mixins.ADCalendar(),
        background: theme.mixins.ADCalendar(),
      },
    },
    eventBtnColor: {
      backgroundColor: SIMILAR_SEARCH_EVENT_TAG_COLOR,
    },
    dropdown: {
      padding: `${pxToRem(40)} ${pxToRem(23)}`,

      '& .MuiInputBase-root': {
        '&:hover, &:focus': {
          background: theme.palette.action.disabledBackground,
        },
        '& .MuiSelect-root': {
          '&:hover, &:focus': {
            background: 'transparent',
          },
        },
      },
    },
    mobileFooter: {
      display: 'flex',
      position: 'fixed',
      width: '100%',
      bottom: '0',
      left: '0',
      height: pxToRem(72),
      background: 'rgb(246, 246, 246)',
      justifyContent: 'space-between',
      alignItems: 'center',
      ...paddingLR(pxToRem(24), pxToRem(24)),

      '& button': {
        padding: `${pxToRem(8)} ${pxToRem(25)}`,
        ...theme.typography.body2Bold,
        color: theme.mixins.White(),

        '& .MuiButton-label': {
          '& .MuiButton-endIcon': {
            marginLeft: 8,
          },
        },
      },
    },
    priceRangeText: {
      color: theme.mixins.Black(600),
    },
    resetButton: {
      position: 'absolute',
      top: pxToRem(25),
      ...left(pxToRem(24)),
    },
    filterText: {
      fontFamily: theme.typography.fontFamily.primaryBold,
      color: theme.mixins.neroBlack(700),
    },
    viewAllMobileBtn: {
      position: 'fixed',
      bottom: pxToRem(16),
      ...left(pxToRem(44)),
      zIndex: 1,

      '& button': {
        backgroundColor: theme.mixins.Black(1000),
      },
    },
    desktopViewAllSection: {
      backgroundColor: theme.mixins.White(1000),
      marginBottom: pxToRem(24),
      padding: pxToRem(24),
      boxShadow: theme.shadows[7],
    },
    listingText: {
      display: 'flex',
      alignItems: 'baseline',
      marginBottom: pxToRem(20),

      '& p': {
        ...marginLR(pxToRem(3), 0),
      },
    },
  };
});

export default useStyles;
